<template>
  <ul class="word-list">
    <li class="word-item"
    @click="chooseCity(word)"
    v-for="(item, word) of cities"
    :key="word">{{word}}</li>
  </ul>
</template>

<script>
export default {
  data () {
    return {}
  },
  props: {
    cities: Object
  },
  methods: {
    chooseCity (word) {
      this.$emit('chooseCity', word)
    }
  }
}
</script>

<style lang="scss" scoped>
  .word-list {
    position: fixed;
    right: 0;
    bottom: 0;
    top: 160px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 40px;
    text-align: center;
    .word-item {
      line-height: 44px;
      color: #00bcd4;
    }
  }
</style>



