<template>
  <div class="header">
    <router-link to="/" class="iconfont icon-left go-back"></router-link>
    城市选择
  </div>
</template>

<script>
export default {
  data () {
    return {}
  }
}
</script>

<style lang="scss" scoped>
  .header {
    width: 100%;
    height: 88px;
    background: #00bcd4;
    line-height: 88px;
    text-align: center;
    color: #fff;
    position: relative;
    font-size: 32px;
    .go-back{
      font-size:40px;
      position: absolute;
      left: 10px;
      top: 0;
      color: #fff;
    }
  }
</style>



