<template>
  <div>
    <city-header></city-header>
    <city-search
    :cities="cities"></city-search>
    <city-area
    :keyword="word"
    :list="hotCities"
    :cities="cities"
    ></city-area>
    <city-word
    @chooseCity="chooseCity"
    :cities="cities"></city-word>
  </div>
</template>
<script>
import CityHeader from './components/header'
import CitySearch from './components/search'
import CityArea from './components/area'
import CityWord from './components/word'
import {getCity} from '@/api'
export default {
  data () {
    return {
      hotCities: [],
      cities: {},
      word: ''
    }
  },
  components: {
    CityHeader,
    CitySearch,
    CityArea,
    CityWord
  },
  mounted () {
    getCity().then(res => {
      if (res.ret && res.data) {
        let data = res.data
        this.hotCities = data.hotCities
        this.cities = data.cities
      }
    })
  },
  methods: {
    chooseCity (word) {
      this.word = word
    }
  }
}
</script>
<style lang="scss" scoped></style>



